        border-radius: 10px;
        /* background-color: green; */
    }
    .ADQ {
        padding-top: 2.5px;
        font-size: 13px;
        padding-bottom: 2.5px;
        margin-top: 15px;
        float: right;
        margin-right: 20px;
    }
    .Aansu {
        margin-right: 0px;
    }
    .Adminabut {
        padding-top: 3px;
