        background-color: #6EB571;
}

.PLogoutBut {
        width: 35%;
        margin: auto;
        margin-top: 15px;
        /* background-color: blue; */
}

.PLogoutBut Button {
        position: static;
        margin-left: 0px;
        border-radius: 20px;
        color: white;
        padding-left: 13px;
        padding-right: 13px;
        border: 2px solid white;
}

.PNav {
        width: 100%;
        height: 200px;
        /* background-color: pink; */
        margin-top: 20px;
}

.PNav a {
        width: 100%;
        height: 45px;
        margin-top: 5px;
        padding-left: 30px;
        padding-top: 9px;
        display: block;
        text-decoration: none;
        color: white;
        /* background-color: rgb(31, 141, 209); */
}

.PNav a:hover {
        background-color: rgb(31, 141, 209);
}

/* **********************************Activity Style********************************* */

.ActivityNav {
        width: 100%;
        height: 40px;
        margin-top: 6px;
