}

.ActivityNav a {
        display: block;
        /* width:7%; */
        /* background-color: pink; */
        height: 100%;
        text-align: center;
        margin-left: 1px;
        margin-right: 1px;
        text-decoration: none;
        padding-top: 7px;
        padding-left: 10px;
        padding-right: 10px;
        float: left;
        color: black;
        /* border-right: 0.1px solid black; */
}

.ActivityNav a:hover {
        background-color: rgba(241, 237, 237, 0.993);
}

